.font-samples {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.font-sample {
    padding: 15px;
    border-radius: 6px;
    background-color: #f8f9fa;
    border: 1px solid #eee;
}

.font-sample h5 {
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 16px;
}

.font-sample p {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 14px;
    color: #666;
}

.sample-text {
    font-size: 18px;
    padding: 10px;
    border-radius: 4px;
    background-color: white;
    border: 1px solid #ddd;
}

.default-font .sample-text {
    font-family: var(--font-default);
}

.lexend-font .sample-text {
    font-family: var(--font-lexend);
}

.opendyslexic-font .sample-text {
    font-family: var(--font-opendyslexic);
}

.roboto-font .sample-text {
    font-family: var(--font-roboto);
}

.opensans-font .sample-text {
    font-family: var(--font-opensans);
}

